'use client';
import React, { useEffect, useRef, useState } from 'react';

const SLIDES = [
  '/images/banner1.jpg',
  '/images/banner2.jpg',
  '/images/banner3.jpg',
];

export default function BannerClient() {
  const [index, setIndex] = useState(0);
  const slideIntervalRef = useRef<number | null>(null);

  // carousel auto
  useEffect(() => {
    slideIntervalRef.current = window.setInterval(() => {
      setIndex(i => (i + 1) % SLIDES.length);
    }, 6000);
    return () => {
      if (slideIntervalRef.current) window.clearInterval(slideIntervalRef.current);
    };
  }, []);

  // dots click
  const goTo = (i: number) => {
    setIndex(i);
    if (slideIntervalRef.current) {
      window.clearInterval(slideIntervalRef.current);
      slideIntervalRef.current = null;
    }
  };

  // Particle Canvas
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const animRef = useRef<number | null>(null);
  const particlesRef = useRef<any[]>([]);
  const mouseRef = useRef({ x: null as number | null, y: null as number | null, radius: 100 });

  useEffect(() => {
    const canvas = canvasRef.current!;
    if (!canvas) return;
    const ctx = canvas.getContext('2d')!;
    let w = (canvas.width = window.innerWidth);
    let h = (canvas.height = window.innerHeight);

    const initParticles = () => {
      const particles = [];
      for (let i = 0; i < 60; i++) {
        particles.push({
          x: Math.random() * w,
          y: Math.random() * h,
          r: Math.random() * 2 + 1,
          dx: (Math.random() - 0.5) * 0.3,
          dy: (Math.random() - 0.5) * 0.3,
          hueOffset: Math.random() * 40
        });
      }
      particlesRef.current = particles;
    };

    const draw = () => {
      ctx.clearRect(0, 0, w, h);
      const timeHue = 250 + Math.sin(Date.now() / 2000) * 20;
      particlesRef.current.forEach(p => {
        const hsl = `hsl(${timeHue + p.hueOffset},80%,65%)`;
        ctx.beginPath();
        ctx.shadowBlur = 15;
        ctx.shadowColor = hsl;
        ctx.fillStyle = hsl;
        ctx.arc(p.x, p.y, p.r, 0, Math.PI * 2);
        ctx.fill();

        if (mouseRef.current.x && mouseRef.current.y) {
          const dx = p.x - mouseRef.current.x!;
          const dy = p.y - mouseRef.current.y!;
          const dist = Math.sqrt(dx * dx + dy * dy);
          if (dist < mouseRef.current.radius) {
            const angle = Math.atan2(dy, dx);
            const force = (mouseRef.current.radius - dist) / mouseRef.current.radius;
            p.x += Math.cos(angle) * force * 2;
            p.y += Math.sin(angle) * force * 2;
          }
        }

        p.x += p.dx; p.y += p.dy;
        if (p.x < 0) p.x = w;
        if (p.x > w) p.x = 0;
        if (p.y < 0) p.y = h;
        if (p.y > h) p.y = 0;
      });
      ctx.shadowBlur = 0;
      animRef.current = requestAnimationFrame(draw);
    };

    initParticles();
    draw();

    const onResize = () => {
      w = canvas.width = window.innerWidth;
      h = canvas.height = window.innerHeight;
      initParticles();
    };
    const onMove = (e: MouseEvent) => {
      mouseRef.current.x = e.clientX;
      mouseRef.current.y = e.clientY;
    };
    const onLeave = () => {
      mouseRef.current.x = null;
      mouseRef.current.y = null;
    };
    window.addEventListener('resize', onResize);
    window.addEventListener('mousemove', onMove);
    window.addEventListener('mouseleave', onLeave);
    window.addEventListener('touchmove', (e) => {
      if (e.touches && e.touches.length > 0) {
        mouseRef.current.x = e.touches[0].clientX;
        mouseRef.current.y = e.touches[0].clientY;
      }
    });
    window.addEventListener('touchend', onLeave);

    return () => {
      if (animRef.current) cancelAnimationFrame(animRef.current);
      window.removeEventListener('resize', onResize);
      window.removeEventListener('mousemove', onMove);
      window.removeEventListener('mouseleave', onLeave);
      window.removeEventListener('touchend', onLeave);
    };
  }, []);

  return (
    <section className="banner" id="home">
      <div className="slides">
        {SLIDES.map((src, i) => (
          <div
            key={i}
            className={`slide ${i === index ? 'active' : ''}`}
            style={{ backgroundImage: `url(${src})` }}
          />
        ))}
      </div>

      <canvas id="particleCanvas" ref={canvasRef} />

      <div className="banner-content">
        <h1>Premium Limited Statues</h1>
        <p>Every piece is handcrafted by skilled artisans</p>
      </div>

      <div className="dots">
        {SLIDES.map((_, i) => (
          <div key={i} className={`dot ${i === index ? 'active' : ''}`} onClick={() => goTo(i)} />
        ))}
      </div>
    </section>
  );
}